import cPicker from "../c-picker/c-picker.vue"
import cpImg from "../c-upload/cpimg.vue"
import {picker} from "../mixins/picker.js"
function oneOf (value, validList) {
	for (let i = 0; i < validList.length; i++) {
	  if (value === validList[i]) {
		return true;
	  }
	}
	throw new Error('mode无效，请选择有效的mode!');
	return false;
}
export default {
	mixins:[picker],
	components:{
		cPicker,
		cpImg
	},
	props:{
		// 主体
		mode:{
			type:String,
			validator(mode){
				let modeList=['','text','number','idcard','switch','textarea','password','img','selector','half','date', 'dateTime', 'yearMonth','time','region','selector','limit','limitHour','range']
				return oneOf(mode,modeList);
			},
			default:"",
		},
		disabled:{
			type:Boolean,
			default:false,
		},
		// 样式类
		cstyle:{
			type:String,
			default:"",
		},
		size:{    // medium small mini
			type:String,
			default:"small",
		},
		flex:{
			type:Boolean,
			default:true,
		},
		align:{		// left center right
			type:String,
			default:'left',
		},
		// 文字类
		title:{			
			type:String,
			default:"默认占位符",
		},
		dataText:{
			default:"默认占位符",
			type:[String , Number , Boolean],
		},
		placeholder:{
			type:String,
		},
		maxlength:{
			type:[Number,String],
			default:200
		},
		rightText:{
			type:String,
		},
		// 状态
		showNumber:{
			type:Boolean,
			default:true,
		},
		clear:{
			type:Boolean,
			default:false,
		},
		// 修饰类
		rightIcon:{
			type:String,
			default:'cuIcon-right'
		},
		// mode = 'img'
		imgType:{  				//shou -展示  add - 正常添加  edit-修改
			type:String,
			default:'add',
		},
		subTitle:{
			type:String,
			default:'',
		},
		imgL:{
			type:Number,
			default:9,
		},
		imgs:{
			type:Array,
			default:()=>[]
		},
		cpimgSize:{		//照片大小超过此值就压缩，且最大宽高等于maxWidth
			type:Number,
			default:500
		},				
		cpimgMaxWidth:{//照片宽高超过此值就压缩，且最大宽高等于此值
			type:Number,
			default:750
		},			
		cpimgQl:{//照片压缩比 0-1	
			type:Number,
			default:0.92
		},				
		cpimgType:{
			type:String,
			default:'url'
		},				//照片压缩后返回的格式  base64	
		cpimgFixOrientation:{
			type:Boolean,
			default:true
		},	//是否修正图片方向（暂时只支持H5）
		cpimgNumber:{
			type:Number,
			default:1
		}				//一次压缩图片数量（不支持H5）
		
	},
	data() {
		return {
			inputValue:undefined,
			textateaL:0,
			cimgs:this.imgs,
			
			nData:'请选择',
			
			modeList:['half','date', 'dateTime', 'yearMonth','time','region','selector','limit','limitHour','range']
		};
	},
	watch:{
		dataText(val){
			// if(this.mode == 'text' || this.mode == 'number' || this.mode == 'idcard' || this.mode == 'textarea'){
			// 	this.inputValue = this.dataText
			// }
			console.log("dataText");
			console.log(val);
			this.inputValue = this.dataText
			this.nData = this.dataText ? this.dataText : this.placeholder ? this.placeholder : '点击选择'+ this.title
		}
	},
	created() {
		this.inputValue=this.dataText;
	},
	methods:{
		toggle(val){
			if(this.disabled){
				return
			}
			this.$refs[val].show();
		},
		hand(value){
			this.$emit('input',value.result)
			this.$emit('getData',value)
		},
		handRight(){
			this.$emit('handRight')
		},
		switchChange(value){
			this.$emit('hand',value.detail.value)
		},
		
		// 
		handInput(value){
			let val = value.detail.value;
			if(this.mode == 'textarea'){
				this.textateaL = val.length;
			}
			this.$emit('input',val)
		},
		handBlur(value){
			let val = value.detail.value;
			// if(this.mode == 'idcard'){
			// 	if(!this.cardPatt.test(val)){
			// 		uni.showToast({ title:'请正确输入身份证号码', icon:'none' ,mask:true,duration:2000})
			// 		return
			// 	}
			// }
			this.$emit('blur',val)
		},
		
		// img
		// 图片选择
		ChooseImage() {
			this.$refs.cpimg._changImg()
		},
		// 
		cpimgOk(val){
			if(val){
				this.cimgs = this.cimgs.length != 0 ? this.cimgs.concat(val) : val;
				this.$emit('hand',this.cimgs)
			}
		},
		cpimgErr(val){
			console.log(val)
		},
		// 删除图片
		DelImg(e) {
			uni.showModal({
				title: '提示',
				content: '确定要删除该项吗？',
				cancelText: '再看看',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.cimgs.splice(e.currentTarget.dataset.index, 1)
						this.$emit('hand',this.cimgs)
					}
				}
			})
		},
		// 预览图片
		ViewImage(e) {
			uni.previewImage({
				urls: this.cimgs,
				current: e
			});
		},
	}
}
